// 短信设置相关接口
import axios from './../fetch'

// 短信设置编辑
export const smsUpdate = params => { return axios.post('/admin/platform/sms/update', params) }

// 短信设置查询单个
export const smsGet = params => { return axios.get('/admin/platform/sms/get', { params: params }) }



