<template>
  <div >
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>设置系统使用的短信服务商信息</li>
        <li>可开启/关闭系统短信服务</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData"  label-width="210px" style="margin-top:20px">
        <el-form-item label="是否开启短信服务：" prop="isEnable" >
          <cms-switch v-model="formData.isEnable"></cms-switch>
          <div class="gray">设置开启，系统可使用短信服务</div>
        </el-form-item>
        <el-form-item label="服务商：" prop="serviceProvider" :error='errorMessage.serviceProvider'>
          <el-radio-group v-model="formData.serviceProvider" >
            <el-radio :label="1">阿里云</el-radio>
          <el-radio :label="2">腾讯云</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="AccessKeyID / AppID：" prop="accessKey" :error='errorMessage.accessKey'>
          <el-input v-model="formData.accessKey" type="textarea" :autofocus="true" ref="first" ></el-input>
          <div class="gray">点击获取<a :href="helpUrl" target="_blank">AccessKey和AccessKeySecret</a></div>
        </el-form-item>
        <el-form-item label="AccessKeySecret/AppKey：" prop="accesskeySecret" :error='errorMessage.accesskeySecret'>
          <el-input v-model="formData.accesskeySecret" type="textarea" ></el-input>
        </el-form-item>
        <el-form-item label="短信签名：" prop="smsSign" :error='errorMessage.smsSign'>
          <el-input  v-model="formData.smsSign" ></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="saveData()"  v-color >保存</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { smsUpdate, smsGet } from '@/api/setting/sms.js'
var defaultData = {
  'id': '',
  'accessKey': '',
  'accesskeySecret': '',
  'serviceProvider': '',
  'smsSign': ''
}
export default {
  name: 'shortMessageShow',
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        accessKey: [rules.required(), rules.english()],
        accesskeySecret: [rules.required(), rules.english()],
        serviceProvider: [rules.required()],
        smsSign: [rules.required()]
      }
    }
  },
  computed: {
    helpUrl() { // 阿里云与腾讯云接口
      return this.formData.serviceProvider === 1 ? 'https://www.aliyun.com/product/sms' : 'https://cloud.tencent.com/product/sms'
    }
  },
  methods: {
    getFormData() {
      smsGet().then(res => {
        this.formData = res.data
      })
    },
    saveData() { // 添加
      this.updateForm(smsUpdate, this.formData, '', '', '', false)
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'list') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getFormData()
  }
}
</script>
<style>
.gray a {
  color: #169BD5;
}
</style>




